import { Component, OnInit, Input} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import {UniversityService} from '../university.service';
import {Title} from "../entity";

@Component({
  selector: 'app-add-title',
  templateUrl: './add-title.component.html',
  styleUrls: ['./add-title.component.css']
})
export class AddTitleComponent implements OnInit {
  @Input() title: Title;
  constructor(
    private route: ActivatedRoute,
    private universityService: UniversityService,
    private location: Location
  ) { }

  ngOnInit() {
  }
  save(description: string, no: string, remarks: string): void { //多个文本框数据
    this.title = {id: null,no: no.trim(), description: description.trim(), remarks: remarks.trim()};
    if (!description) { return; }
    this.universityService.addTitle(this.title)
      .subscribe(() => this.goBack());
  }

  goBack(): void {
    this.location.back();
  }

}

